import { useState } from "react";

export default function AuthPage() {
    const [isLogin, setIsLogin] = useState(true)

    const styles = {
        container: `min-h-screen flex items-center justify-center bg-gray-100`,
        card: `w-full max-w-lg bg-white rounded-2xl shadow-lg p-8`,
        title: `text-2xl font-semibold text-center mb-6`,
        form: `flex flex-col gap-4`,
        input: `border rounded-lg px-4 py-2 focus:outline-none fcus:ring-2 focus:ring-blue-400`,
        row: `flex gap-2`,
        button: `bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600`,
        switch: `text-center mt-6 text-sm`,
        link: `text-blue-500 hover:underline`,
        note: `text-xs text-yellow-500`,
    }
    return (
        <div className={styles.container}>
            <div className={styles.card}>
                <h1 className={styles.title}>
                    {isLogin ? "ng nhp" : "ng k ti khon"}
                </h1>

                <form className={styles.form}>
                    {/* Register */}
                    {!isLogin && (
                        <>
                            <input className={styles.input} placeholder="H v tn" />
                            <input className={styles.input} placeholder="S in thoi" />
                            <input className={styles.input} placeholder="Email" />

                            <div className={styles.row}>
                                <input className={styles.input} placeholder="X" />
                                <input className={styles.input} placeholder="Tnh/Thnh ph" />
                            </div>

                            <input className={styles.input} placeholder="Tn ng nhp" />

                            <select className={styles.input}>
                                <option value="">---Loi khch hng---</option>
                                <option value="personal">C nhn</option>
                                <option value="organization">T chc</option>
                            </select>

                            <p className={styles.note}>
                                * Mt khu s c to t ng v gi v email  nhp
                            </p>
                        </>
                    )}

                    {/* Login */}
                    {isLogin && (
                        <>
                            <input className={`${styles.input}, flex-1`} placeholder="Tn ng nhp" />
                            <input className={`${styles.input}, flex-1`} type="password" placeholder="Mt khu" />
                        </>
                    )}

                    <button 
                        type="submit"
                        className={styles.button}    
                    >
                        {isLogin ? "ng nhp" : "ng k"}
                    </button>
                </form>

                <div className={styles.switch}>
                    {isLogin ? (
                        <span>
                            Cha c ti khon?{" "}
                            <button onClick={() => setIsLogin(false)} className={styles.link}>
                                ng k
                            </button>
                        </span>
                    ) : (
                        <span>
                             c ti khon?{" "}
                            <button onClick={() => setIsLogin(true)} className={styles.link}>
                                ng nhp
                            </button>
                        </span>
                    )}
                </div>
            </div>
        </div>
    )
}